.gallery {
  padding-top: 40px;
  padding-bottom: 40px;
  background-image: linear-gradient($secondary-background-color 44%, $priority-background-color 0%);
}

.gallery__list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.gallery__img-box:not(:last-child) {
  margin-right: 10px;
}

.gallery__item {
  .individual-unit:nth-child(2) {
    margin-top: 10px;
  }
}

.gallery__item_max-width {
  max-width: 144px;
}

.individual-unit {
  border: solid 2px #ffa5ba;
  border-radius: 10px;
  padding: 5px;
  background-color: #ffffff;

  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1), border 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.individual-unit:hover {
  transform: scale(1.1);
  border: 2px solid#d41443;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}

