@media screen and (max-width: 767px) {
  .gallery {
    padding-top: 50px;
    padding-bottom: 40px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .gallery {
    padding-top: 70px;
    padding-bottom: 60px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery {
    padding-top: 70px;
    padding-bottom: 60px;
  }
}
